.container {
  background-image: url(/static/media/texto-estilo-letreros-neon-arcade-night_118419-1578.11a8fde9.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  justify-content: center;
  align-content: center;
  /* margin-top: 310px; */
  height: 100vh;
  width: 100vw;
}

#startbtn {
  margin-top: 680px;
  display: flex;
  justify-content: center;
  align-content: center;
  border-radius: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: transparent;
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #fff;
}
#startbtn:hover {
  border: 0.2rem solid #fff;
  border-radius: 0.5rem;
