}

.gamesGrid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-gap: 5px;
  grid-auto-rows: minmax(100px, auto);
  padding-top: 20px;
}

.games {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-bottom: 15px;
  align-items: center;
}

.games:hover {
